.communication {
  margin-bottom: 23px;

  @media (min-width: @tablet-width) {
    padding: 35px 0;
    padding-bottom: 41px;
    margin: 0;
  }

  @media (min-width: @desktop-width) {
    padding: 36px 61px;
    padding-right: 175px;
    padding-bottom: 40.5px;
  }
}

.communication .sorting-type__title {
  @media (min-width: @tablet-width) {
    margin-top: 32px;
    margin-right: 39px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 34px;
  }
}

.communication .sorting-type__title--tel {
  margin-bottom: 0;

  @media (min-width: @tablet-width) {
    margin-right: 9px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 0;
    width: 101px;
  }
}

.communication .sorting-type__title--email {
  margin-bottom: 10px;

  @media (min-width: @tablet-width) {
    margin-top: 20px;
    margin-right: 9px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 0;
    width: 101px;
  }
}

.communication .sorting-type__title--extra {
  margin-bottom: 22px;

  @media (min-width: @tablet-width) {
    margin-right: 8px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 5px;
  }
}

.communication-extra {
  margin: 0;

  @media (min-width: @tablet-width) {
    padding-top: 39px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 37px;
  }
}

.communication-section {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.communication__field {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 11px;
  margin-bottom: 12px;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    align-items: center;
    margin-bottom: 9.5px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 11px;
  }
}

.field__title {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 17px;
  line-height: 18px;
  color: #000000;

  @media (min-width: @tablet-width) {
    font-size: 19px;
    line-height: 18px;
    margin-right: 25px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 2px;
    margin-right: 23px;
  }
}

.communication-condition {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  margin: 0;
  margin-top: 5px;

  @media (min-width: @tablet-width) {
    font-size: 17px;
    line-height: 30px;
    margin-top: 2px;
  }
}

.communication__text {
  padding: 16px 0;
  padding-bottom: 13px;
  outline: none;
  border: 2px solid transparent;
  border-bottom: 2px solid #000000;
  font-size: 17px;
  line-height: 18px;
  letter-spacing: 0.4px;
  cursor: pointer;

  @media (min-width: @tablet-width) {
    font-size: 19px;
    line-height: 18px;
    padding: 17px 18px;
    padding-bottom: 12px;
    width: 100%;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 12px;
    padding-left: 15px;
  }
}

.communication__text:hover,
.communication__text:focus {
  border-bottom: 2px solid #62d1ba;
  box-shadow: inset 0 -2px 0 #62d1ba;
}

.communication__text:active {
  border-bottom: 2px solid #62d1ba;
}

.communication__text--tel,
.communication__text--email {
  width: inherit;

  @media (min-width: @tablet-width) {
    background-color: #f1f1f1;
  }
}

.communication__text--tel {
  font-size: 15.5px;
  letter-spacing: 0;
  padding-top: 15px;
  padding-bottom: 18px;
  margin-bottom: 13px;

  @media (min-width: @tablet-width) {
    padding-top: 17px;
    padding-bottom: 15px;
    font-size: 19px;
    line-height: 18px;
    letter-spacing: -1.3px;
    margin-top: 10px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 14px;
  }
}

.communication__text--email {
  padding-top: 8px;
  padding-bottom: 14px;
  margin-bottom: 13px;

  @media (min-width: @tablet-width) {
    padding-bottom: 13px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 7px;
    padding-bottom: 14px;
  }
}

.field-email .field__title,
.field-tel .field__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  flex-direction: row;
  width: inherit;
  position: relative;
  margin: 0;

  @media (min-width: @tablet-width) {
    margin-left: 24px;
  }
}

.field-tel {
  margin-top: 0;
}

.communication__icon {
  position: absolute;
  right: 8px;
  top: 15px;
  fill: #000000;
  opacity: 0.2;

  @media (min-width: @tablet-width) {
    top: 28px;
    right: 27px;
  }

  @media (min-width: @desktop-width) {
    right: 9px;
  }
}

.communication__icon--email {
  right: 4px;
  top: 8px;

  @media (min-width: @tablet-width) {
    top: 13px;
    right: 23px;
  }

  @media (min-width: @desktop-width) {
    right: 5px;
  }
}

.communication__icon:hover,
.communication__icon:active {
  fill: #62d1ba;
  opacity: 1;
}

.sorting-type__group--email {
  @media (min-width: @tablet-width) {
    padding-top: 0;
    padding-bottom: 28px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 28px;
  }
}

.sorting-type__group--email .field {
  margin-top: 6px;
}

.sorting-type__group--tel {
  padding-bottom: 0;

  @media (min-width: @tablet-width) {
    padding-top: 33px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 34px;
  }
}

.additionally-text {
  font-family: "Open Sans", "Arial", sans-serif;
  border: 2px solid #000000;
  padding: 15px;
  letter-spacing: 0;
  font-size: 17px;
  line-height: 18px;
  resize: none;

  @media (min-width: @tablet-width) {
    width: 100%;
    font-size: 19px;
    line-height: 18px;
    letter-spacing: inherit;
  }

  @media (min-width: @desktop-width) {
    width: 100%;
  }
}

.communication__text .additionally-text {
  border: 2px solid #000000;
  box-shadow: none;
}

.communication__text:focus {
  border: 2px solid #62d1ba;
}

.additionally-text:hover,
.additionally-text:active {
  border-bottom: none;
  border: 2px solid #62d1ba;
  box-shadow: none;
}

.communication-bottom {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 56.5px;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 47px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 175px;
    padding-right: 175px;
  }
}

.communication-button {
  margin-top: 30px;
  padding: 15px 40px;
  font-size: 17px;
  line-height: 16px;
  width: 100%;

  @media (min-width: @tablet-width) {
    width: auto;
    order: -1;
    margin: 3px 0;
    padding: 16px 29.5px;
  }
}
