.video__tittle {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  padding-left: 25px;
  width: 160px;
  margin: 0;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 24px;
    width: auto;
    padding-left: 35px;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
    margin-top: 28px;
  }
}

.video__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  padding-bottom: 26px;
  margin-left: 24px;

  @media (min-width: @tablet-width) {
    padding: 20px 40px;
    padding-top: 36px;
    order: 1;
    align-self: flex-start;
    margin: 0;
  }

  @media (min-width: @desktop-width) {
    flex-direction: column;
    order: 0;
    align-items: flex-start;
    padding: 46px 57px;
    padding-left: 77px;
    padding-bottom: 20px;
  }
}

.video__image {
  border: none;
  background-color: #ffffff;
  padding: 0;
  position: relative;

  @media (min-width: @desktop-width) {
    grid-row-start: 1;
    grid-row-end: -1;
    grid-column-start: 1;
    grid-column-end: 2;
  }
}

.video__image::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100.6px;
  height: 76.8px;
  content: url("../img/play.svg");
}

.video__image:hover::before {
  transform: scale(1.2) translate(-50%, -50%);
}

.video__image:active::before {
  opacity: 0.5;
  transform: scale(0.8) translate(-50%, -50%);
}

.video__img {
  width: 100%;

  @media (min-width: @tablet-width) {
    order: -1;
    min-height: 455px;
  }
}

.video__text {
  margin: 32px 33px;
  margin-right: 10px;
  font-size: 13px;
  line-height: 24px;

  @media (min-width: @tablet-width) {
    padding: 0 5px;
    margin-top: 4px;
    margin-right: 33px;
    order: 2;
    font-size: 15px;
    line-height: 30px;
  }

  @media (min-width: @desktop-width) {
    order: 0;
    padding: 0;
    margin-top: -10px;
    margin-left: 78px;
  }
}

.video__catalog-button {
  padding: 14px 18px;
  margin-top: 10px;
  margin-bottom: 31px;
  text-align: center;
  .link-reset;

  @media (min-width: @tablet-width) {
    min-width: 584px;
    margin-top: 9px;
    margin-bottom: 45px;
    padding: 14px 18px;
    order: 3;
    box-sizing: border-box;
    font-size: 17px;
    line-height: 16px;
  }

  @media (min-width: @desktop-width) {
    margin-left: 79px;
    margin-bottom: 59px;
    min-width: 230px;
    width: 230px;
    padding: 14px 18px;
    order: 0;
    box-sizing: border-box;
    font-size: 17px;
  }
}

.video__catalog-button:hover .form-link,
.video__catalog-button:active .form-link {
  color: #ffffff;
}

.video__catalog-button:disabled .form-link {
  color: rgba(255, 255, 255, 0.3);
}
